/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
.UI_body {
  /* HTML5 display-role reset for older browsers */
}
.UI_body div,
.UI_body span,
.UI_body applet,
.UI_body object,
.UI_body iframe,
.UI_body h1,
.UI_body h2,
.UI_body h3,
.UI_body h4,
.UI_body h5,
.UI_body h6,
.UI_body p,
.UI_body blockquote,
.UI_body pre,
.UI_body a,
.UI_body abbr,
.UI_body acronym,
.UI_body address,
.UI_body big,
.UI_body cite,
.UI_body code,
.UI_body del,
.UI_body dfn,
.UI_body em,
.UI_body img,
.UI_body ins,
.UI_body kbd,
.UI_body q,
.UI_body s,
.UI_body samp,
.UI_body small,
.UI_body strike,
.UI_body strong,
.UI_body sub,
.UI_body sup,
.UI_body tt,
.UI_body var,
.UI_body b,
.UI_body u,
.UI_body i,
.UI_body center,
.UI_body dl,
.UI_body dt,
.UI_body dd,
.UI_body ol,
.UI_body ul,
.UI_body li,
.UI_body fieldset,
.UI_body form,
.UI_body label,
.UI_body legend,
.UI_body table,
.UI_body caption,
.UI_body tbody,
.UI_body tfoot,
.UI_body thead,
.UI_body tr,
.UI_body th,
.UI_body td,
.UI_body article,
.UI_body aside,
.UI_body canvas,
.UI_body details,
.UI_body embed,
.UI_body figure,
.UI_body figcaption,
.UI_body footer,
.UI_body header,
.UI_body hgroup,
.UI_body menu,
.UI_body nav,
.UI_body output,
.UI_body ruby,
.UI_body section,
.UI_body summary,
.UI_body time,
.UI_body mark,
.UI_body audio,
.UI_body video {
  margin: 0;
  padding: 0;
  border: 0;
  font: inherit;
  font-size: 100%;
  vertical-align: baseline;
}
.UI_body article,
.UI_body aside,
.UI_body details,
.UI_body figcaption,
.UI_body figure,
.UI_body footer,
.UI_body header,
.UI_body hgroup,
.UI_body menu,
.UI_body nav,
.UI_body section {
  display: block;
}
.UI_body body {
  line-height: 1;
}
.UI_body ol,
.UI_body ul {
  list-style: none;
}
.UI_body blockquote,
.UI_body q {
  quotes: none;
}
.UI_body blockquote:before,
.UI_body blockquote:after,
.UI_body q:before,
.UI_body q:after {
  content: "";
  content: none;
}
.UI_body table {
  border-collapse: collapse;
  border-spacing: 0;
}